/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import { jsx } from 'theme-ui';
import { Link as GatsbyLink } from 'gatsby';
import { Flex, Box, Button } from 'rebass';
import strings from './strings.json';
import Article from './Article';

const { ariaBlogViewAll } = strings;

const MainPageBlog = (props) => {
  const { blog, data } = props;
  return (
    <Box width={1} sx={{ position: 'relative' }} >
      <hr sx={{ marginBottom: '50px' }} />
      {blog.logo ? <Flex justifyContent={'center'} mb={3, 4, 5}><img src={blog.logo} alt={blog.name} sx={{ width: ['40vw', '15vw', '10vw'] }} /></Flex> : '' }
      <Flex className="card-deck" flexDirection={[ 'column', 'row', 'row']}>
        {blog.children.map((block, index) => {
          if (block.type === 'article' && data.articles.nodes.filter(article => article.handle === block.handle )[0] ) {
            return (
              <Box className="card border-0" key={index}>
                <Article
                  article={
                    data.articles.nodes.filter(
                      article => article.handle === block.handle
                    )[0]
                  }
                  textColor={block.textColor}
                  textBgColor={block.textBgColor}
                />
              </Box>
            );
          } else {
            return '';
          }
        })}
      </Flex>
      <Box width={1} textAlign={"center"}>
        <Button bg={"primary"} className="rounded-0 font-weight-light text-uppercase" padding={['3vw 10vw', '2vw 6vw', '1vw 3vw']} fontSize={['3.5vw', '2vw', '1vw']} letterSpacing={['.6vw', '.4vw', '.2vw']}>
          <GatsbyLink to={'/blog/journal'}>{ ariaBlogViewAll }</GatsbyLink>
        </Button>
      </Box>
    </Box>
  );
};

export default MainPageBlog;
