/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box, Text, Button } from 'rebass';
import { Link as GatsbyLink } from 'gatsby';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import './carousel.css';

import styled from '@emotion/styled/macro';
import strings from './strings.json';

const { ariaNextButtonLabel, ariaBackButtonLabel, ariaShopButtonLabel } = strings;

const Flag = styled(Box)`
  content: '';
  position: absolute;
  background: transparent;
  left: 0;
  transform: translate(0, 0);
  width: inherit;
  height: 0;
  bottom: 0;
  border-left: 50vw solid white;
  border-right: 50vw solid white;
`;

const MainPageCarousel = (props) => {
  const { carousel } = props;
  return (
    <Box width={1} sx={{ position: 'relative', textAlign: ['center', 'left'] }}>
      <CarouselProvider
        naturalSlideWidth={1300}
        naturalSlideHeight={2600}
        totalSlides={carousel.children.length}
        isPlaying
        infinite
      >
        <Slider>
          {carousel.children.map((slide, index) => {
            return (
              <Box key={index}>
                <Slide key={index} index={index} sx={{ paddingBottom: ['230vw!important', '80vw!important', '60vw!important']}}>
                  <Box sx={{ position: 'absolute' }}>
                    <img
                      src={slide.bg}
                      alt={slide.name}
                      sx={{
                        minWidth: '100vw',
                        height: ['230vw', '80vw'],
                        position: 'relative'
                      }}
                    />
                  </Box>
                  <Flex flexDirection={['column', 'row']} sx={{ position: 'relative' }} py={['25vw', null, '20vw']} px={['5vw', '15vw']}>
                    <Box width={[1, 1 / 2]} order={['2', '1']}>
                      <Text fontSize={['29px', '5vw', '4.5vw']} mb={['3vw', '2vw']} lineHeight={'1.25'}>{slide.header}</Text>
                      <Text fontSize={['5vw', '2vw', '1.2vw']} mb={['3vw']}>{slide.subheader}</Text>
                      <Button bg={"primary"} className="rounded-0 font-weight-light text-uppercase" mb={'2vw'} padding={['6vw 10vw', '2vw 5vw', '1.4vw 3vw']} fontSize={['3.5vw', '1.5vw', '1vw']} letterSpacing={['.6vw', '.2vw']} sx={{ width: 'fit-content', fontWeight: '500' }}>
                        <GatsbyLink to={slide.link}>{ariaShopButtonLabel}</GatsbyLink>
                      </Button>
                    </Box>
                    <Box width={[1, 1 / 2]} order={['1', '2']}>
                      <img src={slide.src} sx={{ width: '100%', padding: ['3rem', '0 2vw', '0 6vw'] }} />
                    </Box>
                  </Flex>
                </Slide>
              </Box>
            );
          })}
        </Slider>
        <Flag sx={{ borderTop: ['10vw solid transparent', '5vw solid transparent'] }}>
          <img src="//cdn.shopify.com/s/files/1/0117/9005/7534/files/fruit2-3.png" alt="" sx={{ position: 'absolute', transform: 'translate(-50%, -50%)', left: '50%', zIndex: 0, width: ['40vw', '25vw', '20vw'] }} />
        </Flag>

        <ButtonBack
          aria-label={ariaBackButtonLabel}
          sx={{
            display: 'block',
            position: 'absolute',
            bottom: ['145vw', '25vw'],
            left: [10, 24, 50],
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: 0,
          }}
        >
          <img src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow.png' alt='previous' sx={{ height: ['5vw', null, '3vw'] }} />
        </ButtonBack>

        <ButtonNext
          aria-label={ariaNextButtonLabel}
          sx={{
            display: 'block',
            position: 'absolute',
            bottom: ['145vw', '25vw'],
            right: [10, 24, 50],
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: 0,
          }}
        >
          <img src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow2.png' alt='next' sx={{ height: ['5vw', null, '3vw'] }} />
        </ButtonNext>

        <DotGroup
          sx={{
            display: ['block', 'none'],
            position: 'absolute',
            bottom: '15vw',
            transform: 'translate(-50%, 0)',
            left: '50%',
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: '2',
            px: '3',
          }}
        />
      </CarouselProvider>
    </Box>
  );
};

export default MainPageCarousel;
