/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import { jsx } from 'theme-ui';
import { Link as GatsbyLink } from 'gatsby';
import { Flex, Box, Button, Text } from 'rebass';
import strings from './strings.json';
import styled from '@emotion/styled/macro';

const { ariaGiftSetsButtonLabel } = strings;

const StyledBox = styled(Box)`
  & img {
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const MainPageHeader = (props) => {
  const { header } = props;

  return (
    <Box bg={'background'} position={'relative'} px={['5vw', '4vw']}>
      <Box width={1} sx={{ position: 'relative' }}>
        <Box sx={{ content: '""', background: 'url(https://cdn.shopify.com/s/files/1/0117/9005/7534/files/round-border.png) center/contain no-repeat',  height: ['45vw', '20vw', '15vw'], width: ['45vw', '20vw', '15vw'], position: ['relative', 'absolute', 'absolute'], transform: ['translate(-50%, 0)', 'translate(-50%, -50%)', 'translate(-50%, -50%)'], top: [0, '-10vw', '-8vw'], left: ['50%', 'unset'], right: ['unset', '-5vw', 0], zIndex: '2' }}>
          <Text fontSize={['3vw', '1.5vw', '1vw']} sx={{ fontWeight: '500', textAlign: 'center', lineHeight: ['1.33', '1.33', '1.5'], color: '#00662E', position: 'relative', top: '50%', transform: 'translateY(-50%)' }}>
            N°1<br/>
            Shea Butter<br/>
            Cosmetic Brand
          </Text>
        </Box>
      </Box>
      <Box textAlign={'center'}>
        <Text fontSize={['6vw', '4vw']} padding={['6vw 0', '0 15vw 3vw']} fontWeight={'body'} lineHeight={'heading'}>{header.header}</Text>
        <Text fontSize={['4vw', '2vw', '1.5vw']} padding={[0, '0 25vw']} fontWeight={'light'} lineHeight={'body'}>{header.subheader}</Text>
      </Box>
    </Box>
  );
};

export default MainPageHeader;
