/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyLink from 'gatsby-link';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Flex, Box, Text, Heading } from 'rebass';
import IndividualProduct from '../catalog/IndividualProduct';
import NoImage from '../../components/Icons/NoImage';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import './carousel.css';
import substrDescription from '../../utils/substrDescription';
import strings from './strings.json';

const { ariaNextButtonLabel, ariaBackButtonLabel } = strings;

const CardWrapper = styled(Box)`
  & img {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center center;
    opacity: 1;
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const TabCollectionBlock = props => {
  const {
    handle,
    title,
    products,
  } = props.collection;

  return (
    <Box width={1}>
      <Flex width={1} py={['3vw']} sx={{display: ['none', 'flex']}}>
        {products.length > 0 ? products.map((product, index) => {
          if (index <= 1) {
            return (
              <Box width={[1, 1 / 3, 1 / 4]} mr={'2vw'} key={index} sx={{ overflow: 'hidden' }}>
                <GatsbyLink to={product.fields.shopifyThemePath}>
                  <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                    {product.fields.firstImage ? (
                      <Box sx={{ position: 'relative' }}>
                        <img src={product.fields.firstImage.originalSrc} alt={product.handle} sx={{ padding: ['3vw', '2vw'] }} />
                        <Box sx={{ width: '100%', pb: '100%' }}></Box>
                      </Box>
                    ) : (
                      <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                    )}
                    {product.title ? (
                      <Text sx={{ overflow: "hidden", height: ['15vw', '7vw', '3vw'], marginBottom: "10px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>{substrDescription(product.title, 60)}</Text>
                    ) : (
                      <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                    )}
                    {product.priceRange ? (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>{product.priceRange.minVariantPrice.amount}</Text>
                    ) : (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
                    )}
                  </CardWrapper> 
                </GatsbyLink>
              </Box>
            );
          } else if (index == 2 ) {
            return (
              <Box width={[1, 1 / 3, 1 / 4]} mr={[0, null, '2vw']} key={index} sx={{ overflow: 'hidden' }}>
                <GatsbyLink to={product.fields.shopifyThemePath}>
                  <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                    {product.fields.firstImage ? (
                      <Box sx={{ position: 'relative' }}>
                        <img src={product.fields.firstImage.originalSrc} alt={product.handle} sx={{ padding: ['3vw', '2vw'] }} />
                        <Box sx={{ width: '100%', pb: '100%' }}></Box>
                      </Box>
                    ) : (
                      <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                    )}
                    {product.title ? (
                      <Text sx={{ overflow: "hidden", height: ['15vw', '7vw', '3vw'], marginBottom: "10px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>{substrDescription(product.title, 60)}</Text>
                    ) : (
                      <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                    )}
                    {product.priceRange ? (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>{product.priceRange.minVariantPrice.amount}</Text>
                    ) : (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
                    )}
                  </CardWrapper> 
                </GatsbyLink>
              </Box>
            );
          } else if (index == 3) {
            return (
              <Box width={[1, null, 1 / 4]} display={['block', 'none', 'block']} m={0} key={index} sx={{ overflow: 'hidden' }}>
                <GatsbyLink to={product.fields.shopifyThemePath}>
                  <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                    {product.fields.firstImage ? (
                      <Box sx={{ position: 'relative' }}>
                        <img src={product.fields.firstImage.originalSrc} alt={product.handle}sx={{ padding: ['3vw', '2vw'] }} />
                        <Box sx={{width: '100%', pb: '100%' }}></Box>
                      </Box>
                    ) : (
                      <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                    )}
                    {product.title ? (
                      <Text sx={{ overflow: "hidden", height: ['15vw', '7vw', '3vw'], marginBottom: "10px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>{substrDescription(product.title, 60)}</Text>
                    ) : (
                      <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                    )}
                    {product.priceRange ? (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>{product.priceRange.minVariantPrice.amount}</Text>
                    ) : (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
                    )}
                  </CardWrapper> 
                </GatsbyLink>
              </Box>
            );
          } else {
          };
        }) : (
          <Flex>
            <Box width={[1, null, 1 / 4]} mr={'2vw'} sx={{ overflow: 'hidden' }}>
              <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
              </CardWrapper> 
            </Box>
            <Box width={[1, null, 1 / 4]} mr={'2vw'} sx={{ overflow: 'hidden' }}>
              <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
              </CardWrapper> 
            </Box>
            <Box width={[1, null, 1 / 4]} mr={[0, null, '2vw']} sx={{ overflow: 'hidden' }}>
              <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
              </CardWrapper> 
            </Box>
            <Box width={[1, null, 1 / 4]} display={['block', 'none', 'block']} m={[0]} sx={{ overflow: 'hidden' }}>
              <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                <NoImage color="grey" p={['3vw', '2vw']} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
              </CardWrapper> 
            </Box>
          </Flex>
        )
      }
      </Flex>
      <Flex width={1} py={'10vw'} sx={{ position: 'relative', display: ['block', 'none'] }}>
        <CarouselProvider
          naturalSlideWidth={1300}
          naturalSlideHeight={800}
          totalSlides={products.length}
          isPlaying
          infinite
        >
          <Slider>
            {products.length > 0 ? products.map((product, index) => {
              return (
                <Box key={index}>
                  <Slide key={index} index={index} sx={{ pb: '100vw!important' }}>
                    <Box width={[1, 1 / 3, 1 / 4]} mr={'2vw'} key={index} sx={{ overflow: 'hidden' }}>
                      <GatsbyLink to={product.fields.shopifyThemePath}>
                        <CardWrapper py={['7vw', '4vw', '3vw']} px={['7vw', '2vw']} bg={'background'}>
                          {product.fields.firstImage ? (
                            <Box sx={{ position: 'relative' }}>
                              <img src={product.fields.firstImage.originalSrc} alt={product.handle} sx={{ padding: '10vw' }} />
                              <Box sx={{width: '100%', pb: '100%' }}></Box>
                            </Box>
                          ) : (
                            <NoImage color="grey" p={'10vw'} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                          )}
                          {product.title ? (
                            <Text sx={{ overflow: "hidden", height: ['15vw', '7vw', '3vw'], marginBottom: "10px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>{substrDescription(product.title, 60)}</Text>
                          ) : (
                            <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                          )}
                          {product.priceRange ? (
                            <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>{product.priceRange.minVariantPrice.amount}</Text>
                          ) : (
                            <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
                          )}
                        </CardWrapper> 
                      </GatsbyLink>
                    </Box>
                  </Slide>
                </Box>
              )
            }) : (
              <Box width={1}>
                <CardWrapper py={['10vw', '4vw', '3vw']} px={['10vw', '2vw']} bg={'background'}>
                  <NoImage color="grey" p={'10vw'} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                  <Text mb={4} sx={{ fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                  <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: ['4vw', '1.5vw', '1vw'], fontWeight: "300", textAlign: "center", }}>99.99</Text>
                </CardWrapper> 
              </Box>
            )}
          </Slider>

          <ButtonBack aria-label={ariaBackButtonLabel} sx={{ display: 'block', position: 'absolute', bottom: '60%', left: 10, 'z-index': '2', border: 0, bg: 'transparent', color: 'primary', py: 2, px: 0 }} >
            <img src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow.png' alt='previous' sx={{ height: '8vw' }} />
          </ButtonBack>

          <ButtonNext aria-label={ariaNextButtonLabel} sx={{ display: 'block', position: 'absolute', bottom: '60%', right: 10, 'z-index': '2', border: 0, bg: 'transparent', color: 'primary', py: 2, px: 0 }} >
            <img src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow2.png' alt='next' sx={{ height: '8vw' }} />
          </ButtonNext>

          <DotGroup sx={{ display: 'block', textAlign: 'center', 'z-index': '2', border: 0, bg: 'transparent', color: 'primary', py: '2', px: '3' }} />
        </CarouselProvider>
      </Flex>
    </Box>
  );
};

export default TabCollectionBlock;
