/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { jsx } from 'theme-ui';
import { Flex, Box, Text, Heading } from 'rebass';
import styled from '@emotion/styled/macro';
import strings from '@gatsbystorefront/gatsby-theme-storefront-shopify/src/templates/main/strings';

import substrExcerpt from '@gatsbystorefront/gatsby-theme-storefront-shopify/src/utils/substrExcerpt';

const { ariaBlogButtonLabel } = strings;

const StyledBox = styled(Box)`
  & img {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center center;
    opacity: 1;
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const Flag = styled(Box)`
  content: '';
  position: absolute;
  background: transparent;
  left: 0;
  transform: translate(0, 0);
  width: inherit;
  height: 0;
  bottom: 0;
`;

const Article = props => {
  const {
    title,
    excerpt,
    image,
    fields: { shopifyThemePath },
  } = props.article;
  const { textColor = 'primary', textBgColor = 'white' } = props;

  return (
    <StyledBox sx={{ position: 'relative' }}>
      <Flex sx={{ flexDirection: ['column'], justifyContent: 'flex-end', alignItems: ['center'], width: '100%', height: '100%' }}>
        <Box>
          <Box sx={{ position: 'relative', overflow: 'hidden' }}>
            {image ? (
              <Box>
                <Box>
                  <div sx={{width: '100%', paddingBottom: '70%' }}></div>
                  <img src={image.src} alt={title} sx={{ width: '100%' }} />
                </Box>
                <Flag sx={{ borderTop: ['10vw solid transparent', '5vw solid transparent'], borderLeft: ['50vw solid white', '25vw solid white'], borderRight: ['50vw solid white', '25vw solid white'], marginX: ['-5vw', '-3vw'] }}></Flag>
              </Box>
            ) : (
              <Box pt="60%" />
            )}
          </Box>

          <Box px={['1vw', '3vw', '4vw']} py={'1rem'} textAlign={'center'} sx={{ backgroundColor: [textBgColor, 'transparent'] }}>
            <Text className="card-title" fontSize={['6vw','2.5vw']} fontWeight={'400'} lineHeight={['1.5', '1.5', '1.33']} mb={10}>
              {title}
            </Text>
            <Text sx={{ margin: '0 auto 20px', fontSize: ['4vw', '1.7vw', '1.4vw'], lineHeight: '1.5' }}>
              <p>{substrExcerpt(excerpt, 120)}</p>
            </Text>
            <Text as={GatsbyLink} to={shopifyThemePath} mt={'1rem'} mb={'2rem'} sx={{ textTransform: 'uppercase', fontSize: ['4vw', '1.5vw', '1.1vw'], letterSpacing: '.2vw' }}>
              { ariaBlogButtonLabel }
            </Text>
          </Box>
        </Box>
      </Flex>
    </StyledBox>
  );
};

export default Article;
