/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import GatsbyLink from 'gatsby-link';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Flex, Box, Text, Heading } from 'rebass';
import strings from './strings.json';

const { ariaShopButtonLabel } = strings;

const StyledBox = styled(Box)`
  & img {
    position: absolute;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center center;
    opacity: 1;
    transition: opacity 500ms ease 0s;
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const CollectionBlock = props => {
  const {
    title,
    image,
    fields: { shopifyThemePath },
  } = props.collection;
  const { textColor = 'primary', textBgColor = 'white', bgColor = 'primary' } = props;

  return (
    <StyledBox sx={{ position: 'relative', backgroundColor: 'inherit' }}>
      <Box>
        <Box sx={{ position: 'inherit', top: '-7vw', padding: ['10vw', '3vw'], zIndex: '1' }}>
          {image ? (
            <div>
              <div sx={{width: '100%', paddingBottom: '100%' }}></div>
              <img src={image.src} alt={title} sx={{ top: ['-5vw', '-6vw', '-6.5rvw'], padding: ['20vw', '2vw'], width: '100%' }} />
            </div>
          ) : (
            <Box pt="100%" />
          )}
        </Box>

        <Box
          sx={{
            position: ['absolute'],
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            zIndex: '2',
          }}
          backgroundColor={"transparent"}
        >
          <Flex
            className="collectionWrap"
            sx={{
              flexDirection: ['column'],
              justifyContent: 'flex-end',
              alignItems: 'center',
              width: '100%',
              height: '100%',
            }}
          >
            <Box
              width={'auto'}
              sx={{
                mt: 'auto', 
                mb: '10%',
                backgroundColor: 'transparent',
                opacity: [0.8, 1],
                px: ['3%', 0],
                py: ['2%', 0],
                textAlign: 'center',
              }}
            >
              {' '}
              <Heading
                fontSize={['8vw', '4vw', '2.5vw']}
                color={'primary'}
                sx={{
                  fontWeight: '400',
                  textShadow: '0 0 20px rgba(0,0,0,0.4)',
                }}
              >
                {title}
              </Heading>
              <Text
                as={GatsbyLink}
                to={shopifyThemePath}
                fontSize={['3.5vw', '1.5vw', '1vw']}
                sx={{
                  fontWeight: '500',
                  textShadow: '0 0 20px rgba(0,0,0,0.4)',
                  color: textColor,
                  textDecoration: 'none',
                  textTransform: 'uppercase'
                }}
              >
                {ariaShopButtonLabel}
              </Text>
            </Box>
          </Flex>
        </Box>
      </Box>
    </StyledBox>
  );
};

export default CollectionBlock;
