/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Link as GatsbyLink } from 'gatsby';
import { Flex, Box, Text, Button } from 'rebass';
import strings from '@gatsbystorefront/gatsby-theme-storefront-shopify/src/templates/main/strings';

const {ariaPromoButtonLabel} = strings;

const MainPageBanner = (props) => {
  const { banner } = props;

  return (
    <Flex sx={{ position: 'relative', overflow: 'hidden' }}>
      <Box sx={{ position: 'absolute' }}>
        {banner.src ? 
          <img
            src={banner.src}
            alt={banner.name}
            sx={{
              minWidth: '100vw',
              height: 'auto',
              position: 'relative',
              left: ['-40%', '-30%', 0]
            }}
          />
        : <Box bg='#d8a071'></Box>}
      </Box>
      <Box width={[1, '70vw']} padding={['15vw 10vw 5vw', '10vw 7vw 3vw', '7vw 7vw 2vw']} color={'#fff'} textAlign={['center', 'left', 'left']} sx={{ position: 'relative' }}>
        <img src={banner.logo} alt='sponsor logo' sx={{ maxWidth: ['50vw', '20vw', '15vw'] }} />
        <Text mb={'1vw'} mt={[20, 0]} fontSize={['8vw', '5vw']} sx={{ textShadow: '0 0 20px rgba(0,0,0,0.4)' }} lineHeight={[1.5, 1.33, 1.33]}>{banner.header}</Text>
        <Text mb={'4vw'} fontSize={['5vw', '2vw', '1.5vw']}>{banner.subheader}</Text>
        <Button bg={"primary"} className="rounded-0 font-weight-light text-uppercase" mb={'2vw'} padding={['6vw 10vw', '2vw 5vw', '1.4vw 3vw']} fontSize={['3.5vw', '2vw', '1vw']} letterSpacing={['.6vw', '.4vw', '.2vw']} sx={{ width: 'fit-content', fontWeight: '500' }}>
          <GatsbyLink to={banner.link}>{ariaPromoButtonLabel}</GatsbyLink>
        </Button>
        <Text fontSize={['4vw', '1.8vw', '1.2vw']}>{banner.note}</Text>
      </Box>
    </Flex>
  );
};

export default MainPageBanner;
