/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Flex, Box, Text } from 'rebass';

const Flag = styled(Box)`
  content: '';
  position: absolute;
  background: transparent;
  left: 0;
  transform: translate(0, 0);
  width: inherit;
  height: 0;
  bottom: 0;
  border-left: 50vw solid white;
  border-right: 50vw solid white;
`;

const MainPageSiteDesc = (props) => {
  const { siteDesc } = props;
  
  return (
    <Box sx={{ position: 'relative' }}>
      <Flex className="card-deck" p={['5vw', '5vw 3vw', '5vw 3.5vw']} backgroundColor={siteDesc.bgColor} sx={{ flexDirection: ['column', 'row', 'row'], marginX: ['auto', 24, 50] }}>
        {siteDesc.children.map((desc, index) => {
          return (
            <Box width={[1, 1 / 3]} py={[4, 0]} px={['5vw', '2.7vw', '3vw']} textAlign={['center']} key={index}>
              <img sx={{ width: ['50vw', '20vw', '10vw'], marginBottom: ['1rem', '2rem', '2rem'] }} src={desc.src} alt={desc.name} />
              <Text mb={2} sx={{ fontSize: ['7vw', '2.2vw', '1.7vw'], textTransform: 'capitalize'}}>{desc.header}</Text>
              <Text sx={{ fontSize: ['4vw', '2vw', '1.2vw']}}>{desc.subheader}</Text>
            </Box>
          );
        })}
      </Flex>
      <Flag sx={{ borderTop: ['10vw solid transparent', '5vw solid transparent'] }}></Flag>
    </Box>
  );
};

export default MainPageSiteDesc;
