/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import { jsx } from 'theme-ui';
import { Button, Box, Text } from 'rebass';
import { Link as GatsbyLink } from 'gatsby';
import TabCollectionBlock from './TabCollectionBlock';
import styled from '@emotion/styled/macro';
import strings from './strings.json';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

const { ariaBestSellers, ariaShopAll } = strings;

const StyledBox = styled(Box)`
  & nav {
    justify-content: center;

    a.nav-link {
      text-transform: capitalize;
      padding: 2vw 7vw;
    }

    a.nav-link.active {
      font-weight: 600;
      border: none;
      border-bottom: 2px solid;
      background-color: transparent;
    }
  }
`

const MainPageTabs = (props) => {
  const { tabs, data } = props;
  return (
    <Box width={1} bg={'backgroundGrey'} p={['15vw 5vw', '10vw 5vw']} sx={{ position: 'relative' }}>
      <Text pb={['5vw', '3vw']} fontSize={['8vw', '4vw', '2.5vw']} color={'primary'} sx={{ fontWeight: '400', textAlign: 'center', textTransform: 'capitalize' }}>{ariaBestSellers}</Text>
      <StyledBox>
        <Tabs defaultActiveKey="face">
          {tabs.children.map((block, index) => {
            if (block.type === 'collection' && data.tabs.nodes.filter(collection => collection.handle === block.handle )[0] ) {
              return (
                <Tab key={index} eventKey={block.handle} title={block.handle} style={{ fontSize: ['4vw', '2vw', '1.5vw'] }}>
                  <TabCollectionBlock
                    collection={
                      data.tabs.nodes.filter(
                        collection => collection.handle === block.handle
                      )[0]
                    }
                  />
                </Tab>
              );
            } else {
              return '';
            }
          })}
        </Tabs>
        <Box width={1} textAlign={"center"}>
          <Button bg={"primary"} className="rounded-0 text-uppercase font-weight-light" padding={['6vw 10vw', '2vw 5vw', '1.4vw 3vw']} fontSize={['3.5vw', '2vw', '1vw']} letterSpacing={['.6vw', '.4vw', '.2vw']} sx={{ margin: ['auto', 'unset' ], width: 'fit-content', fontWeight: '500' }}>
            <GatsbyLink to={'/collection/all'}>{ ariaShopAll }</GatsbyLink>
          </Button>
        </Box>
      </StyledBox>
    </Box>
  );
};

export default MainPageTabs;
