/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box, Heading } from 'rebass';
import { Link as GatsbyLink } from 'gatsby';
import styled from '@emotion/styled/macro';
import IgIcon from '../../components/Icons/Instagram';

const StyledBox = styled(Box)`
  & img {
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const MainPageInstagram = (props) => {
  const { instagram } = props;
  
  return (
    <Flex bg={'background'} sx={{ position: 'relative', flexDirection: 'column', margin: 'auto' }}>
      <Heading fontSize={'xx-large'} fontWeight={'300'} textAlign={'center'} mb={'2rem'}>
        <Flex justifyContent={"center"} fontWeight={"400"}>
          <IgIcon sx={{ position: "relative", top: "10px", mr: "10px" }}/>
          institutkarite
        </Flex>
      </Heading>
      <Box display={['inline-block', 'flex', 'flex']}>
        {instagram.children.map((image, index) => {
          return (
            <StyledBox className="ig-pic" p={'1vw'} width={[1 / 2, 1 / 4, 1 / 5]} display={['inline-block', 'block']} key={index}>
              <GatsbyLink to={image.link}>
                <img sx={{ width: '100%'}} src={image.src} alt="" />
              </GatsbyLink>
            </StyledBox>
          );
        })}
      </Box>
    </Flex>
  );
};

export default MainPageInstagram;
