import React from 'react';
import { Flex, Box, Text } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';

import MainPageCarousel from './MainPageCarousel';
import MainPageHeader from './MainPageHeader';
import MainPageCollectionBlock from './MainPageCollectionBlock';
import MainPageTabs from './MainPageTabs';
import MainPageProductBlock from './MainPageProductBlock';
import MainPageBanner from './MainPageBanner';
import MainPageTextImageBlock from './MainPageTextImageBlock';
import MainPageBlog from './MainPageBlog';
import MainPageSiteDesc from './MainPageSiteDesc';
import MainPageInstagram from './MainPageInstagram';

const MainPage = (props) => {
  const dataQuery = useStaticQuery(graphql`
    query MainPageStaticQuery {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            mainPage {
              handle
              type
              name
              src
              logo
              header
              subheader
              note
              link
              bgColor
              textBgColor
              textColor
              children {
                handle
                type
                name
                src
                header
                subheader
                link
                bg
                textBgColor
                textColor
                bgColor
              }
            }
          }
        }
      }
    }
  `);

  const { mainPage } = dataQuery.site.siteMetadata.gatsbyStorefrontConfig;

  const { data } = props;

  const { gatsbyImageProps } = data.store.siteMetadata.gatsbyStorefrontConfig;

  return (
    <Flex flexWrap="wrap" mx="auto">
      {mainPage.map((block, index) => {
        if (block.type === 'carousel') {
          return (
            <Box width={1} key={index}>
              <MainPageCarousel
                carousel={block}
                data={data}
              />
            </Box>
          );
        } else if (block.type === 'header') {
          return (
            <Box width={[1]} pt={['10vw', null, '8vw']} px={['5vw', '4vw', '5vw']} key={index}>
              <MainPageHeader header={block} data={data} />
            </Box>
          );
        } else if (block.type === 'collection') {
          return (
            <Box width={1} bg={'background'} p={['5vw', '5vw 3vw', '5vw 3.5vw']} sx={{ position: 'relative' }} key={index}>
              <MainPageCollectionBlock collection={block} data={data} />
            </Box>
          );
        } else if (block.type === 'banner') {
          return (
            <Box width={[1]} py={'5vw'} key={index}>
              <MainPageBanner banner={block} />
            </Box>
          );
        } else if (block.type === 'tabs') {
          return (
            <Box width={[1]} p={['5vw', '0 3vw', '0 3.5vw']} key={index}>
              <MainPageTabs tabs={block} data={data} />
            </Box>
          );
        }else if (block.type === 'textImage') {
          return (
            <Box width={[1]} p={['5vw', '0 3vw', '0 3.5vw']} key={index}>
              <MainPageTextImageBlock textImage={block} />
            </Box>
          );
        } else if (block.type === 'blog') {
          return (
            <Box width={[1]} p={['5vw', '0 3vw', '0 3.5vw']} key={index}>
              <MainPageBlog blog={block} data={data} />
            </Box>
          );
        } else if (block.type === 'siteDesc') {
          return (
            <Box width={[1]} py={'5vw'} key={index}>
              <MainPageSiteDesc siteDesc={block} />
            </Box>
          );
        } else if (block.type === 'instagram') {
          return (
            <Box width={[1]} p={['5vw', '0 3vw', '0 3.5vw']} key={index}>
              <MainPageInstagram instagram={block} />
            </Box>
          );
        } else {
          return '';
        }
      })}
    </Flex>
  );
};

export default MainPage;
