/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box } from 'rebass';
import CollectionBlock from './CollectionBlock';

const MainPageCollectionBlock = (props) => {
  const { collection, data } = props;
  return (
    <Box width={1} sx={{ position: 'relative' }}>
      <Flex flexDirection={[ 'column', 'row', 'row']}>
        {collection.children.map((block, index) => {
          if (block.type === 'collection' && data.collections.nodes.filter(collection => collection.handle === block.handle )[0] ) {
            return (
              <Box className="collectWrap" sx={{ position: 'relative' }} width={[1, 1 / 3]} mx={[0, '1vw']} my={['1vw', 0]} backgroundColor={block.bgColor} key={index}>
                <div className="decor" sx={{ position: 'absolute', height: ['50vw', '18vw'], top: ['-10vw', '16vw'], left: ['15vw', 0] }}></div>
                <div className="decor2" sx={{ position: 'absolute', height: ['50vw', '18vw'], top: ['-8vw', '2.5vw'], left: ['70vw', '-1.6vw'] }}></div>
                <CollectionBlock
                  collection={
                    data.collections.nodes.filter(
                      collection => collection.handle === block.handle
                    )[0]
                  }
                  textColor={block.textColor}
                  textBgColor={block.textBgColor}
                />
              </Box>
            );
          } else {
            return '';
          }
        })}
      </Flex>
    </Box>
  );
};

export default MainPageCollectionBlock;
